.no_scroll {
  overflow: hidden;
}

.overlay {
  /* opacity: 0; */
  /* visibility: hidden; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 17, 17, 0.4);
  z-index: 20;
  transition: 0.3s all;
}

.modal-main {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 335px;
  border: 2px solid rgba(17, 17, 17, 1);
  border-radius: 18px;
  padding: 40px 24px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;
  background-color: #fff;
}

.modal_btn_close {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 24px;
  height: 24px;
  padding: 0;
  /* padding: 6px; */
  line-height: 0;
  /* background-color: #ffffff; */

  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-cover {
  margin: 0 auto 14px auto;
}

.modal-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #111111;
  margin-bottom: 4px;
}

.modal-author {
  font-style: italic;
  font-size: 12px;
  line-height: 14px;
  color: #b4afaf;
  margin-bottom: 18px;
}

.modal-add-btn {
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  color: #111111;
  background-color: #ffffff;
  margin: 0 auto;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  padding: 14px 28px;
}

.modal-svg {
  stroke: #111111;
}

.modal-description {
  width: 287px;
  color: #111111;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 14px;
}

.modal-books-shops {
  display: flex;
  margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .modal-main {
    box-sizing: border-box;
    min-width: 579px;
    padding: 40px;
  }

  .modal-title {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 8px;
  }

  .modal-author {
    font-size: 14px;
    line-height: 18px;
    color: rgba(180, 175, 175, 1);
    margin-bottom: 20px;
  }

  .modal-description {
    width: 279px;
    color: rgba(17, 17, 17, 1);
    margin-bottom: 20px;
  }

  .modal-add-btn {
    font-size: 18px;
    line-height: 24px;
    margin: 0 auto;
    border: 2px solid #4f2ee8;
    border-radius: 40px;
    width: 499px;
    padding: 20px 0;
  }

  .modal-cover {
    width: 192px;
    height: 281px;
    margin: 0;
  }

  .modal_book_info {
    display: flex;
    margin-bottom: 40px;
    gap: 24px;
  }

  .modal_text_info {
    display: flex;
    flex-direction: column;
  }
}
